#online {
  margin-left: 10px;
}

.shirts {
  opacity: 1;
}

.shirts:hover {
  opacity: .75;
}

a.shirts {
  color: #ccc;
  font-size: 13px;
}

.shirts img {
  height: 40px;
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.85);
		        transform: scale(0.85);
	}

	75% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	100% {
		-webkit-transform: scale(0.85);
		        transform: scale(0.85);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(0.85);
		        transform: scale(0.85);
	}

	75% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	100% {
		-webkit-transform: scale(0.85);
		        transform: scale(0.85);
	}
}
